* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

html {
  font-size: 62.5%;
  font-family: "Poppins", sans-serif;
}

body {
  background-color: #f8f9fa;
  color: #333;
}

.container {
  overflow-y: hidden;
  height: 100vh;
}

/* MAIN NAVIGATION */

.header {
  display: flex;
  justify-content: space-between;
  align-items: center;

  box-shadow: 0 0.2rem 0.8rem 0.1rem rgb(0, 0, 0, 0.2);
  padding: 0 2rem;

  position: sticky;

  top: 0;
  left: 0;
}

.main-nav {
  font-size: 2.2rem;
}

.main-nav-list {
  display: flex;
  align-items: center;
  gap: 2.4rem;
  padding: 2.4rem 0;

  list-style: none;
}

.main-nav-link {
  text-decoration: none;
  color: #333;
  font-weight: 600;
  letter-spacing: 0.05rem;
}

.teamcode-form {
  display: flex;
  justify-content: center;
  align-items: center;
  gap: 1.2rem;
}

.input-teamcode {
  font-size: 1.6rem;
}

.teamcode-label {
  font-size: 1.6rem;
  font-weight: 600;
  text-transform: uppercase;
}

.btn-teamcode {
  height: 3.2rem;
  width: 10rem;
  font-size: 1.8rem;
}

/* .btn-teamcode:hover {
  background: rgba(105, 188, 219, 1);
  cursor: pointer;
} */

.btn-teamcode-help {
  height: 3rem;
  width: 3rem;
  padding: 1rem;
  border-radius: 50%;
  border: none;
  cursor: pointer;
}

.page-content-container {
  display: flex;
  height: 100%;
}

.login-form-container {
  position: absolute;
  top: 50%;
  left: 50%;

  transform: translate(-50%, -50%);

  z-index: 999;
  width: 70%;
  height: 70%;
  border-radius: 1rem;

  background-image: radial-gradient(
    100% 100% at 100% 0,
    #5adaff 0,
    #5468ff 100%
  );

  color: #fff;

  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  gap: 1.2rem;

  box-shadow: 0 1rem 3rem 1rem rgb(0, 0, 0, 0.2);
}

.login-title {
  font-size: 2.4rem;
  text-transform: uppercase;
  margin-bottom: 3.2rem;
}

.input-username,
.input-password {
  /* max-width: 12rem; */
  /* margin-right: 1.2rem; */
  font-size: 1.8rem;
  max-width: 30rem;
  height: 2.4rem;
  align-self: center;
  text-align: center;
  width: 80%;
}

.form-login {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  width: 30rem;
  gap: 1.2rem;
}

.form-login,
.cta-signup,
.cta-signin {
  font-size: 1.4rem;
  font-weight: 600;
  margin-bottom: 1rem;
}

.login-label {
  font-size: 1.8rem;
  margin-right: 0.5rem;
  text-align: center;
}

.cta-signup,
.cta-signin {
  font-size: 1.8rem;

  height: 4.8rem;
  width: 10rem;
}

/* ASIDE */

.main-aside {
  width: 35rem;
  background-color: #f4f5f7;
  height: 100%;

  padding: 2rem;
  border-right: 0.2rem solid rgb(51, 51, 51, 0.1);
}

.project-selected-view {
  display: flex;
  align-items: center;
  gap: 1.2rem;
  margin-bottom: 2.4rem;
}

.project-icon {
  width: 6rem;
  height: 6rem;
  border-radius: 1rem;
  /* background-color: #228be6; */
  background-image: radial-gradient(
    100% 100% at 100% 0,
    #5adaff 0,
    #5468ff 100%
  );
  display: flex;
  align-items: center;
  justify-content: center;
  padding: 1.2rem;
  stroke: #fff;
}

.aside-project-title {
  font-size: 1.8rem;
  font-weight: 500;
}

.aside-project-type {
  color: #555;
  font-size: 1.4rem;
  font-weight: 400;
}

.aside-nav {
  font-size: 1.8rem;
}

.aside-nav-list {
  list-style: none;
  display: flex;
  flex-direction: column;
  justify-content: center;
  gap: 2rem;
}

.aside-nav-item {
  padding: 0.4rem;
  border-radius: 1rem;
  transition: all 0.3s ease;
}

.aside-nav-item:hover {
  background-color: rgb(233, 233, 233);
  cursor: pointer;
}

.aside-nav-link,
.project-nav-link {
  text-decoration: none;
  color: #444;
}

.add-project-button,
.btn-projects-back,
.btn-add-bug {
  width: 18rem;
  height: 3.2rem;
  font-size: 1.8rem;
  margin-bottom: 2rem;
}

/* ADD PROJECT */

.add-project-container {
  border: 1px solid #5468ff;
  padding: 1.2rem 0;

  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  margin-top: 1.2rem;
  gap: 1.2rem;
}

.add-project-title {
  font-size: 1.6rem;
}

/* .add-project-button:hover,
.btn-projects-back:hover,
.btn-add-bug:hover {
  background: rgba(105, 188, 219, 1);
  cursor: pointer;
