/* MAIN CONTENT PANEL */

.section-main {
  padding: 2.4rem;
  width: 100%;

  transition: all 0.3s ease;
}

.text-path {
  font-size: 1.6rem;
  font-weight: 400;
  color: #555;
}

.content-heading {
  font-size: 3rem;
  color: #5468ff;
  margin-bottom: 1.6rem;
}

/* BUGS LIST HEADER */

.bugs-list-header {
  width: auto;
  border-bottom: 0.1rem solid rgb(51, 51, 51, 0.5);
  margin-bottom: 1.2rem;
}

.bugs-list {
  display: grid;
  grid-template-columns: 1fr 2fr 1fr 1fr 0.5fr;
  align-items: center;
  /* justify-items: center; */
  list-style: none;

  transition: all 0.3s ease;
}

.bugs-header-list-item {
  font-size: 1.8rem;
  font-weight: 600;
}

/* USER BUG LIST */

.bugs-list--user {
  font-size: 1.6rem;
  margin-top: 1rem;
  padding: 0.8rem;
  border-radius: 1rem;
}

.status-icon {
  height: 2.4rem;
  width: 2.4rem;
}

.bugs-list--user:hover {
  background-color: rgb(233, 233, 233);
  cursor: pointer;
}

.bug-modal {
  position: absolute;
  top: 50%;
  left: 50%;

  transform: translate(-50%, -50%);

  z-index: 999;
  width: 70%;
  height: 70%;
  border-radius: 1rem;

  /* background-image: radial-gradient(
    100% 100% at 100% 0,
    #5adaff 0,
    #5468ff 100%
  ); */
  background-color: #5468ff;
  color: #fff;

  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  gap: 1.2rem;

  transition: all 0.3s ease;
}

.bug-modal-title {
  font-size: 2.4rem;
  text-transform: uppercase;
}

.secondary-text {
  padding-bottom: 2rem;
  font-size: 1.4rem;
}

.bug-modal-label {
  font-size: 2rem;
  font-weight: 600;
  letter-spacing: 0.05rem;
  margin-bottom: 1rem;
}

.bug-modal-input {
  width: 30rem;
  height: 3rem;
  border-radius: 1rem;
  padding: 1rem;
  border: none;
}

.new-bug-form {
  display: grid;
  grid-template-columns: repeat(2, 1fr);
  gap: 1.4rem;
  text-align: center;
  margin-bottom: 1.2rem;
}

.bug-input-field {
  display: flex;
  flex-direction: column;
}

.input-bug-desc,
.input-bug-tags {
  height: 10rem;
}

.btn-cancel-add-bug,
.btn-submit-bug {
  font-size: 1.8rem;
  height: 4.8rem;
  width: 10rem;
