.selected-bug {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  text-align: center;
  gap: 2rem;
  background-image: radial-gradient(
    100% 100% at 100% 0,
    #5adaff 0,
    #5468ff 100%
  );
  padding: 2rem;
  border-radius: 2rem;
}

.selected-bug-title {
  font-size: 3.2rem;
  color: #fff;
}

.selected-bug-data {
  color: #fff;
  font-size: 1.8rem;
  font-weight: 500;
}

.bug-edit-form {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  gap: 2rem;
  padding: 1.2rem;
}

/*
SPACING SYSTEM (px)
 2 / 4 / 8 / 12 / 16 / 20 / 24 / 32 / 48 / 64 / 80 / 96 / 128

FONT SIZE SYSTEM (px)
10 / 12 / 14 / 16 / 18 / 20 / 24 / 30 / 36 / 44 / 52 / 62 / 74 / 86 / 98
*/

.tint-blue {
  color: #228be6;
}

.tint-red {
  color: red;
}

.tint-green {
  stroke: #51cf66;
  color: #51cf66;
}

.hide {
  opacity: 0;
  visibility: hidden;
}
